import {Component, Injector, OnInit} from '@angular/core';
import DataSource from 'devextreme/data/data_source';
import {BaseComponent} from '../../../@calls/components/base/base.component';
import {Validators} from '@angular/forms';

@Component({
    selector: 'app-departments',
    templateUrl: './departments.component.html',
    styleUrls: ['./departments.component.scss']
})
export class DepartmentsComponent extends BaseComponent implements OnInit {
    dataSource: any;
    departmentForm: any;
    isLoading = false;
    add = true;
    dep_id = '';

    constructor(injector: Injector) {
        super(injector);
        this.departmentForm = this.fb.group({
            label: ['', Validators.compose([Validators.required])],
            number: ['+18654128481', Validators.compose([Validators.required])]
        });
    }

    ngOnInit(): void {
        this.getDepartments();
    }

    async deleteItem(item) {
        const result = await this.utilService.confirmationDialog(this.translateService.instant('app.warning'),
            this.translateService.instant('app.removeCaution'));
        result.subscribe((res) => {
            if (res) {
                this.isLoading = true;
                this.departmentService.removeDepartment(item.id).subscribe((res) => {
                    this.getDepartments();
                    this.isLoading = false;
                }, (err) => {
                    console.log(err);
                    this.isLoading = false;
                    this.utilService.showConnectionErrorToast();
                });
            }
        });
    }

    resetForm() {
        const j = {
            label: '',
            number: '+18654128481'
        };
        this.departmentForm.setValue(j);
        this.add = true;
    }

    getDepartments(): void {
        this.departmentService.getDepartments().subscribe((res) => {
            this.dataSource = new DataSource(res.content.data);
        });
    }

    updateItem(data): void {
        this.add = false;
        const j = {
            label: data.label,
            number: data.number
        };
        this.dep_id = data.id;
        this.departmentForm.setValue(j);
    }

    addDepartment(form, f): void {
        this.isLoading = true;
        form['company_id'] = this.authService.currentUser.company_id;
        if (this.add) {
            this.departmentService.addDepartment(form).subscribe((res) => {
                this.isLoading = false;
                f.resetForm();
                this.getDepartments();
                document.getElementById('close').click();
                this.utilService.showAddedSuccessfullyToast();
            }, (err) => {
                this.isLoading = false;
                console.log(err);
                this.utilService.showConnectionErrorToast();
            });
        } else {
            form['id'] = this.dep_id;
            this.departmentService.updateDepartment(form).subscribe((res) => {
                this.isLoading = false;
                f.resetForm();
                this.getDepartments();
                document.getElementById('close').click();
                this.utilService.showAddedSuccessfullyToast();
            }, (err) => {
                this.isLoading = false;
                console.log(err);
                this.utilService.showConnectionErrorToast();
            });
        }
    }
}
